// ball.ts
import * as planck from "planck";

export class Ball {
  private velocity = 1;
  private damping = 0.3;
  private body: planck.Body;
  private ballColor: string;
  private ballIndex: string;

  get position() {
    return this.body.getPosition();
  }

  constructor(
    private world: planck.World,
    private x: number,
    private y: number,
    private radius: number,
    private speed: number,
    private color: string,
    private index: number
  ) {
    this.body = this.world.createDynamicBody(planck.Vec2(x, y));
    this.body.createFixture(planck.Circle(radius), {
      density: 1.0,
      restitution: 1.0,
      friction: 0,
    });
    this.body.setLinearVelocity(planck.Vec2(0, speed));
    // this.body.setPosition(planck.Vec2(x, y));
    this.ballColor = color;
    this.ballIndex = index.toString();
  }

  public draw(context: CanvasRenderingContext2D) {
    const position = this.body.getPosition();
    const radius = this.body.getFixtureList()!.getShape() as planck.Circle;
    // context.save();
    context.beginPath();
    context.arc(position.x, position.y, radius.getRadius(), 0, Math.PI * 2);
    context.fillStyle = this.ballColor;
    context.fill();

    context.strokeStyle = "white"; //    
    context.lineWidth = 2; //   2 
    context.stroke(); //  .

    context.closePath();
    //  
    context.font = "15px DOSMyungjo-Medium"; //     
    context.textAlign = "center";
    context.textBaseline = "middle";
    context.fillStyle = "white"; //    (  )
    context.fillText("P" + this.ballIndex, position.x, position.y);

    // context.restore();
  }
}
