import { Ball } from "./ball";
import {BallRank} from "../alkagi";
// import { StageDef } from "./data/maps";
import { Vec2 } from "planck";

import {
  bottomMargin,
  canvasHeight,
  initialZoom,
  canvasWidth,
  sideMargin,
  zoomThreshold,
  mapHeight,
} from "../../constants";

export class Camera {
  private _position: Vec2 = new Vec2(canvasWidth / 2, -canvasHeight / 2); //  
  private _targetPosition: Vec2 = new Vec2(); //   
  private _zoom: number = 1; //  / 
  private _targetZoom: number = 1; //   / 

  private _zoomMode: boolean = false;
  get zoom() {
    return this._zoom;
  }
  set zoom(v: number) {
    this._targetZoom = v; //  /  
  }

  get x() {
    return this._position.x; //  x 
  }
  set x(v: number) {
    this._targetPosition.x = v; //  x   
  }
  get y() {
    return this._position.y; //  y 
  }
  set y(v: number) {
    this._targetPosition.y = v; //  y   
  }

  get position() {
    return this._position; //   
  }

  setPosition(v: Vec2, force: boolean = false) {
    if (force) {
      return this._position.set(v); //    
    }
    return this._targetPosition.set(v); //    .
  }

  update({
    balls,
    // stage,
    needToZoom,
    rankedBalls,
  }: {
    balls: Ball[];
    // stage: StageDef;
    needToZoom: boolean;
    rankedBalls: BallRank[];
  }) {
    //    / .
    this._calcTargetPositionAndZoom(
      balls,
      // stage,
      needToZoom,
      rankedBalls
    );

    //   (interpolate).
    this._position.x = this._interpolation(this.x, this._targetPosition.x);
    this._position.y = this._interpolation(this.y, this._targetPosition.y);

    //  /  .
    this._zoom = this._interpolation(this._zoom, this._targetZoom);
  }


  private _calcTargetPositionAndZoom(
    balls: Ball[],
    // stage: StageDef,
    needToZoom: boolean,
    rankedBalls: BallRank[]
  ) {
    const targetIndex = rankedBalls[0].index
    // console.log("1st",rankedBalls[0].index)
    // console.log("2nd",rankedBalls[1].index)

    const dx = (balls[rankedBalls[0].index].position.x - balls[rankedBalls[1].index].position.x)
    const dy = (balls[rankedBalls[0].index].position.y - balls[rankedBalls[1].index].position.y)
    const distance = Math.sqrt(dx*dx +dy*dy)
    if (balls.length > 0) {
      const targetBall = balls[targetIndex] ? balls[targetIndex] : balls[0];
      if (this._zoomMode) {
        if (distance > 160) {
          if (Math.abs(targetBall.position.y) <  canvasHeight / 2 + 30) {
            this.setPosition(new Vec2((canvasWidth) / 2, -canvasHeight / 2));
          } else if (Math.abs(targetBall.position.y) > (mapHeight - 300)) {
            this.setPosition(new Vec2((canvasWidth) / 2, -(mapHeight - 300)));
          } else {
            this.setPosition(new Vec2((canvasWidth) / 2, targetBall.position.y));
          }
          this._zoomMode = false
          this.zoom=1;
        }else{
          this.setPosition(new Vec2(targetBall.position.x, targetBall.position.y));
        }
        } else {
          if (Math.abs(targetBall.position.y) <  canvasHeight / 2 + 30) {
            this.zoom = 1;
            this.setPosition(new Vec2((canvasWidth) / 2, -canvasHeight / 2));
          } else if (Math.abs(targetBall.position.y) > (mapHeight - 300)) {
            this.zoom = 1;
            this.setPosition(new Vec2((canvasWidth) / 2, -(mapHeight - 300)));
          } else if (rankedBalls.length > 2
              && ((Math.abs(targetBall.position.y) > (mapHeight)/3))
              && distance < 150) {
            this.zoom = 1.2;
            this.setPosition(new Vec2(targetBall.position.x, targetBall.position.y));
            this._zoomMode = true;

          } else {
            this.zoom = 1;
            this.setPosition(new Vec2((canvasWidth) / 2, targetBall.position.y));
          }

        }
        if (needToZoom) {

          // const goalDist = Math.abs((canvasHeight-bottomMargin) + this._position.y);
          //
          // this.zoom = Math.max(1, (1 - (goalDist / zoomThreshold)) * 4);
          // console.log("Position Y",this._position.y)
          //
          // // console.log("canvas",canvasWidth)
          // console.log("goalDistance", goalDist)
          // console.log(this.zoom)
          // this.zoom = 1.2;

        } else {
          this.zoom = 1; //  / 
        }
      }

     else {
      this.setPosition(new Vec2(0, 0)); //    
      this.zoom = 1; //  / 
    }
  }

  private _interpolation(current: number, target: number) {
    const d = target - current;
    if (Math.abs(d) < 1 / initialZoom) {
      return target;
    }

    return current + d / 10; //   .
  }

  renderScene(
    ctx: CanvasRenderingContext2D,
    callback: (ctx: CanvasRenderingContext2D) => void
  ) {
    const zoomFactor = initialZoom * 2 * this._zoom; //  /   /  
    ctx.save();
    ctx.translate(-this.x * this._zoom, -this.y * this._zoom - canvasHeight); //     
    ctx.scale(this.zoom, this.zoom); //  /    /
    ctx.translate(
      ctx.canvas.width / zoomFactor,
      ctx.canvas.height / zoomFactor
    ); //   
    callback(ctx); //      
    ctx.restore(); //   .
  }
}

