import { World, Vec2 } from "planck";
import Map from "./elements/map";
import AlkagiRenderer from "./alkagiRenderer";
import {
  bottomMargin,
  canvasHeight,
  canvasWidth,
  mapHeight,
  sideMargin,
  startLineMargin,
} from "../constants";
import { createObjects } from "./createObjects";
import Bumper from "./elements/bumper";
import Booster from "./elements/booster";
import { Camera } from "./elements/camera";
import { Ball } from "./elements/ball";

export type BallRank = {
  distance: number;
  index: number;
  rank: number;
};

export default class Alkagi {
  world: World;
  map: Map;
  renderer: AlkagiRenderer;
  camera: Camera = new Camera();

  ballsOnBoosters: Set<planck.Body> = new Set();

  rankedBalls: BallRank[];
  sortedRankedBalls: BallRank[];
  setSortedRankedBalls: any;
  animationId: number;

  constructor(canvas: HTMLCanvasElement, balls, players, setSortedRankedBalls) {
    this.setSortedRankedBalls = setSortedRankedBalls;
    this.world = new World();
    this.renderer = new AlkagiRenderer(canvas);
    this.map = new Map(this.world, canvas.width, mapHeight);
    if (players) {
      let index = 0;
      for (const player of players) {
        index++;
        let px = canvasWidth / 2 - ((players.length + 1) / 2 - index) * 40;
        let py = bottomMargin + startLineMargin + 1;
        const nball = new Ball(
          this.world,
          px,
          -py,
          14,
          -player.ballSpeed,
          player.color,
          index
        );
        this.renderer.addBall(nball);
      }
    }
    this.setContact();
    const { bumpers, boosters, bumperRects } = createObjects(
      this.world,
      30,
      30
    );

    //    
    for (const booster of boosters) {
      this.renderer.addBooster(booster);
    }

    //     ( renderer addBumper  )
    for (const bumper of bumpers) {
      this.renderer.addBumper(bumper); // NOTE: renderer    .
    }

    for (const bumperRect of bumperRects) {
      this.renderer.addBumperRect(bumperRect); // NOTE: renderer    .
    }

    // this.renderer.addBall(ball);
    // console.log(this.renderer.balls);
    // this.renderer.addBall(ball2);
    // this.renderer.addBall(ball3);
    // console.log(this.renderer.balls);

    this.updateWinner();
    this.renderer.render(this.map, this.camera);
  }
  setContact() {
    this.world.on("begin-contact", (contact) => {
      const fixtureA = contact.getFixtureA();
      const fixtureB = contact.getFixtureB();

      for (const booster of this.renderer.boosters) {
        if (
          fixtureA === booster.body.getFixtureList() ||
          fixtureB === booster.body.getFixtureList()
        ) {
          const ballBody =
            fixtureA === booster.body.getFixtureList()
              ? fixtureB.getBody()
              : fixtureA.getBody();
          this.ballsOnBoosters.add(ballBody);
        }
      }

      for (const bumper of this.renderer.bumpers) {
        if (
          fixtureA === bumper.body.getFixtureList() ||
          fixtureB === bumper.body.getFixtureList()
        ) {
          const ballBody =
            fixtureA === bumper.body.getFixtureList()
              ? fixtureB.getBody()
              : fixtureA.getBody();

          //   .
          const contactPoint = contact.getManifold().points[0].localPoint;

          //      .
          const direction = ballBody
            .getWorldPoint(contactPoint)
            .sub(bumper.body.getWorldCenter());
          direction.normalize();
          //       .
          ballBody.setLinearVelocity(
            direction.mul(1.2 * ballBody.getLinearVelocity().length())
          );
        }
      }
      for (const bumperRect of this.renderer.bumperRects) {
        if (
          fixtureA === bumperRect.body.getFixtureList() ||
          fixtureB === bumperRect.body.getFixtureList()
        ) {
          const ballBody =
            fixtureA === bumperRect.body.getFixtureList()
              ? fixtureB.getBody()
              : fixtureA.getBody();

          //   .
          const contactPoint = contact.getManifold().points[0].localPoint;

          //      .
          const direction = ballBody
            .getWorldPoint(contactPoint)
            .sub(bumperRect.body.getWorldCenter());
          direction.normalize();
          //       .
          ballBody.setLinearVelocity(
            direction.mul(1.2 * ballBody.getLinearVelocity().length())
          );
        }
      }
    });

    this.world.on("end-contact", (contact) => {
      const fixtureA = contact.getFixtureA();
      const fixtureB = contact.getFixtureB();

      for (const booster of this.renderer.boosters) {
        if (
          fixtureA === booster.body.getFixtureList() ||
          fixtureB === booster.body.getFixtureList()
        ) {
          const ballBody =
            fixtureA === booster.body.getFixtureList()
              ? fixtureB.getBody()
              : fixtureA.getBody();

          //    ballsOnBoosters Set 
          this.ballsOnBoosters.delete(ballBody);
        }
      }
    });
  }
  update() {
    this.camera.update({
      balls: this.renderer.balls,
      needToZoom: true, //temp
      rankedBalls: this.rankedBalls,
    });
    for (const ballBody of this.ballsOnBoosters) {
      ballBody.applyForce(Vec2(0, -80), ballBody.getWorldCenter());
    }
    this.renderer.render(this.map, this.camera);
    this.world.step(1 / 1);
    this.animationId = requestAnimationFrame(() => {
      this.updateWinner();
      this.update();
    });
  }

  updateWinner() {
    if (!this.map) return;

    this.sortedRankedBalls = this.renderer.balls

      .map((ball, index) => ({
        index,
        distance: Math.abs(mapHeight + ball.position.y),
      })) //         
      .sort((a, b) => a.distance - b.distance) //    
      .map((obj, rank) => ({ ...obj, rank: rank + 1 })); //   

    if (this.sortedRankedBalls.length > 0) {
      this.rankedBalls = this.sortedRankedBalls;
      this.setSortedRankedBalls(this.sortedRankedBalls); //   
    } else {
      // console.log("No balls to rank.");
    }
  }

  stopUpdate() {
    cancelAnimationFrame(this.animationId);
  }
}
